// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
// import ElementUI from 'element-ui';
import {
  Aside,
  Autocomplete,
  Button,
  ButtonGroup,
  Container,
  Footer,
  Form,
  FormItem,
  Header,
  Icon,
  Image,
  Input,
  Main,
  Select,
  Tooltip,
} from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import App from './App'
// import router from './router'

Vue.config.productionTip = false
// Vue.use(ElementUI);

Vue.use(Autocomplete);
Vue.use(Input);
Vue.use(Select);
Vue.use(Button);
Vue.use(ButtonGroup);
Vue.use(Tooltip);
Vue.use(Form);
Vue.use(FormItem);
Vue.use(Icon);
Vue.use(Container);
Vue.use(Header);
Vue.use(Aside);
Vue.use(Main);
Vue.use(Footer);
// Vue.use(Link);
Vue.use(Image);

new Vue({
  el: '#app',
  render: h => h(App)
});



