<template>
  <div id="app">
    <el-container :style="{height: height + 'px'}">
      <el-header>
        <h2 @click="linkTo">{{ headerLink }}</h2>
      </el-header>
      <el-main :style="{marginTop: marginTop + 'px'}">
        <div>
          <el-input :placeholder="placeholder" v-model="iconUrl" @change="handle" clearable autofocus>
            <template slot="prepend">https://api.xinac.net/icon/?url=</template>
            <template slot="append">
              <el-image :src="iconImg" style="width: 32px; height: 32px"></el-image>
            </template>
          </el-input>
        </div>
      </el-main>
      <el-footer>© {{ year }} <a :href="placeholder">xinac.net</a></el-footer>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      height: 600,
      marginTop: 200,
      year: new Date().getFullYear(),
      iconUrl: '',
      iconImg: 'https://api.xinac.net/icon/default',
      defaultIcon: 'https://api.xinac.net/icon/default',
      placeholder: 'https://www.xinac.net',
      headerLink: 'https://api.xinac.net'
    }
  },
  methods: {
    handle() {
      console.log("表单提交的数据：", this.iconUrl);
      if (this.iconUrl) {
        this.iconImg = 'https://api.xinac.net/icon/?url=' + this.iconUrl;
      } else {
        this.iconImg = this.defaultIcon;
      }

    },
    resize() {
      this.height = document.documentElement.clientHeight;
      this.marginTop = (this.height - 300) / 2
    },
    linkTo() {
      window.location.href = this.headerLink
    }
  },
  mounted() {
    this.resize()
    window.onresize = () => {
      this.resize()
    }
  }
}
</script>

<style>
body {
  margin: auto;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

.el-header {
  color: #fff;
  text-align: center;
  height: 64px !important;
  line-height: 64px !important;
  background: #001529;
}

.el-header > h2 {
  margin: 0;
  cursor: pointer;
}

.el-footer {
  background-color: #EBEEF5;
  text-align: center;
  line-height: 40px !important;
  height: 40px !important;
  border: 1px dashed #ccc;
  color: #2c3e50;
}

.el-footer > a {
  text-decoration-line: none;
}

.el-main {
  color: #333;
  text-align: center;
  margin-top: 200px;
}

.el-input {
  width: 1000px;
}

.el-input > input {
  height: 50px;
  line-height: 50px;
  font-size: large;
}

.el-input > .el-input-group__prepend {
  font-size: large;
  font-weight: 600;
  color: #000;
  padding: 0 1px 0 20px;
}

.el-input > .el-input-group__append {
  padding: 0 15px;
}
</style>



